.cloud-version{--cloud-icon-info: var(--icon-info-dark);--cloud-icon-checkmark: var(--icon-checkmark-dark);--cloud-icon-alert: var(--icon-alert-outline-dark);--cloud-theme-filter: var(--background-invert-if-dark)}.cloud-version.cloud-version-major-24{--cloud-icon-info: var(--icon-info-000);--cloud-icon-checkmark: var(--icon-checkmark-000);--cloud-icon-alert: var(--icon-alert-outline-000);--cloud-theme-filter: none}.flex-container{display:flex}.flex-container.flex-column{flex-direction:column}.flex-container.flex-row{flex-direction:row}.flex-container.flex-center{align-items:center}.settings-section :deep(.app-settings-section){margin-bottom:40px}.settings-section :deep(.settings-section__title){position:relative;padding-left:48px;height:32px}.settings-section :deep(.settings-section__title)::before{content:"";position:absolute;left:0;top:0;width:32px;height:32px;background-size:32px;background-image:url("../img/app.svg");background-repeat:no-repeat;background-origin:border-box;background-position:left center;filter:var(--cloud-theme-filter)}.settings-section .user-id-email-placeholder{font-family:mono-space;font-weight:bold;margin-left:.5em;margin-right:.5em}.settings-section .user-id-email-placeholder::before{content:"—";margin-right:.5em}